/* General Styles */
/* * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
} */

body, html {
  font-family: 'Roboto', sans-serif;
  background-color: #f4f4f4;
  scroll-behavior: smooth;
}

h1, h2, h3, p {
  margin: 0;
}

.ball-text {
  background: linear-gradient(0deg, red, white); 
  -webkit-background-clip: text; 
  color: transparent;
}

/* Navbar */
.navbar {
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  background: #2c3e50;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.navbar .logo h1 {
  position: relative;
  left: -7px;
  top: -3px;
}

.navbar .logo img {
  height: 50px;
  width: 50px;
  object-fit: contain;
}

.nav-links {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 25px;
}

.nav-links li {
  display: inline;
}

.nav-links a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #f5a623;
}

.play-btn {
  background: #f5a623;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s ease, transform 0.2s ease-in-out;
}

.play-btn:hover {
  background-color: #e08b13;
  transform: scale(1.05);
}

.hamburger {
  display: none;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 30px;
  z-index: 2000;
}

/* Hero Section */
.home_hero {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: linear-gradient(rgba(56, 55, 55, 0.5), rgba(31, 31, 31, 0)),
              url("../../assets/landing.jpeg") no-repeat center center/cover;
}

.hero-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(223, 223, 223, 0);
  pointer-events: none;
}

.hero-content {
  position: relative; 
  color: white;
  z-index: 2;
}

.home_hero h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.home_hero p {
  background: linear-gradient(90deg, #2767C2, #7AB1FF, #CEE2FF);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 30px;
}

.home_hero .install-btn {
  padding: 14px 32px;
  font-size: 18px;
  background-color: #f5a623;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.home_hero .install-btn:hover {
  background-color: #e08b13;
  transform: scale(1.05);
}

/* Features Section */
.features {
  padding: 60px 0;
  text-align: center;
  background-color: #fff;
}

.feature-cards {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
}

.feature-card {
  width: 250px;
  height: 250px;
  background: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  max-width: 350px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.feature-card h3 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 15px;
}

.feature-card p {
  font-size: 1rem;
  color: #777;
  line-height: 1.5;
}

/* Steps to Play */
.steps {
  padding: 60px 20px;
  background-color: #f9f9f9;
  text-align: center;
}

.step-list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  flex-wrap: wrap;
}

.step {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 240px;
  height: 240px;
  background: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.step:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.step span {
  font-size: 30px;
  font-weight: bold;
  color: #f5a623;
  margin-bottom: 20px;
}

.step p {
  font-size: 1.2rem;
  color: #333;
  line-height: 1.5;
}

/* Footer */
.footer {
  background-color: #333;
  color: white;
  padding: 15px 20px;
  text-align: center;
}

.footer p {
  font-size: 16px;
  margin-bottom: 20px;
}

.footer-links {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 30px;
  padding: 0;
}

.footer-links li {
  font-size: 14px;
}

.footer-links a {
  text-decoration: none;
  color: #fff;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #f5a623;
}

/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
  .hamburger {
    display: block;
  }

  .nav .logo {
    padding: 10px;
  }

  .nav-links {
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    background: #2c3e50;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
    display: none;
  }

  .nav-links.open {
    display: flex;
  }

  .home_hero {
    height: 90vh;
  }

  .home_hero h1 {
    font-size: 2rem;
  }

  .home_hero p {
    font-size: 1.2rem;
  }

  .features {
    padding: 50px 20px;
  }

  .feature-cards {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
  }

  .feature-card {
    width: 80%;
    height: 100px;
    padding: 15px;
  }

  .step {
    height: 100px;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .step-list {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
  }

  .footer {
    padding: 20px 10px;
  }
}

@media screen and (max-width: 250px) {
  .hamburger {
    display: block;
    font-size: 20px;
  }

  .nav .logo {
    padding: 5px;
    font-size: 12px;
  }

  .nav-links {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background: #2c3e50;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 15px;
    display: none;
  }

  .nav-links.open {
    display: flex;
  }

  .home_hero {
    height: 80vh;
  }

  .home_hero h1 {
    font-size: 1.5rem;
  }

  .home_hero p {
    font-size: 1rem;
  }

  .features {
    padding: 40px 10px;
  }

  .feature-cards {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }

  .feature-card {
    width: 90%;
    height: 80px;
    padding: 10px;
    overflow: hidden; /* Prevent text overflow */
    text-overflow: ellipsis; /* Add ellipsis for overflow text */
    white-space: nowrap; /* Prevent wrapping of text */
  }

  .feature-card h3, .feature-card p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 12px; /* Adjust font size for readability */
  }

  .step {
    font-size: 12px; 
    overflow: hidden;
    height: 80px;
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .step-list {
    font-size: 12px; 
    overflow: hidden;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    flex-wrap: wrap;
  }

  .footer {
    padding: 15px 8px;
  }
}
