*,
*:before,
*:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #080710;
}



.login-container {
    display: flex;
    justify-items: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(10px);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    background-image: url('../../assets/landing.jpeg');
    background-size: cover;
    background-repeat: no-repeat;

}
.form-wrap{
    flex-basis: 400px;
    padding: 10px;
}

.login-container form {
    background:linear-gradient(190deg, #227dfd33 20%, #1f4e13e7 100%); 
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 30px 20px;
    }
.login-container form * {
    font-family: 'Poppins', sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}

.login-container form h3 {
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}

.login-container label {
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
}

.login-container input {
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.07);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
}

.login-container ::placeholder {
    color: #e5e5e5;
}

.login-container button {
    margin-top: 30px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.login-container .social {
    margin-top: 18px;
    display: flex;
    gap: 15px;
    flex-direction: column;
}

.login-container .social div {
    border-radius: 3px;
    color: #eaf0fb;
    text-align: center;
}

.login-container .social div a {
    color: rgb(155, 255, 54);
}
