
*,
*:before,
*:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #080710;
}



.register-container {
    display: flex;
    justify-items: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(10px);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    background-image: url('../../assets/landing.jpeg');
    background-size: cover;
    background-repeat: no-repeat;

}

.form-wrap{
    flex-basis: 400px;
    padding: 10px;
}

.register-container form {
    background:linear-gradient(190deg, #227dfd33 20%, #1f4e13e7 100%); 
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 25px 20px;
    }


.register-container form * {
    font-family: 'Poppins', sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}

.register-container form h3 {
    font-size: 30px;
    font-weight: 500;
    line-height: 36px;
    text-align: center;
}

.register-container label {
    display: block;
    margin-top: 15px;
    font-size: 16px;
    font-weight: 500;
}

.register-container input {
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.07);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
}

.otp-container{
    display: flex;
    /* justify-content: space-around; */
    justify-content: center;
    align-items: center;
    align-content: center;
    gap: 5px;

}

.otp-container .input{

}

.otp-container .generate-btn{
    
    align-content: center;
    flex-basis: 150px;
     padding: 10px 10px;
     font-size: 14px;
}   

.register-container ::placeholder {
    color: #e5e5e5;
}

.register-container button {
    margin-top: 20px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.register-container .social {
    margin-top: 18px;
    display: flex;
    gap: 10px;
    flex-direction: column;
}

.register-container .social div {
    border-radius: 3px;
    color: #eaf0fb;
    text-align: center;
}

.register-container .social div a {
    color: rgb(155, 255, 54);
}
